@import "ScssHelpers/_helpers_all.scss";

.latest_articles {
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding-top: 10rem;
		padding-bottom: 17.6rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 7.2rem;
		padding-bottom: 5rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 2rem;
		padding-bottom: 3.5rem;
	}
}

.latest_articles__head {
	@extend %slider_head;
}

.latest_articles__title {
	@extend %slider_title;
}

.latest_articles__slider {
	@include bp($point_2, min) {
		&::before {
			content: "";
			position: absolute;
			top: -1%;
			left: 2.6rem;
			transform: translateX(-100%);
			z-index: 2;

			width: var(--gl-indent);
			height: 101%;

			background-color: var(--light_color);
		}
	}

	@include bp($point_4, min) {
		overflow: visible;

		margin: 0 -2.6rem;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1rem 4.6rem -1rem;
	}
}

.latest_articles__slider.is-destroy::before {
	display: none;
}
.latest_articles_slide {
	@include bp($point_10, min) {
		.swiper-initialized & {
			&:nth-child(3n + 1) {
				.latest_articles_slide_image::before {
					background-color: var(--blue_color_3);
				}
			}

			&:nth-child(3n + 2) {
				.latest_articles_slide_image::before {
					background-color: var(--orange_color);
				}
			}

			&:nth-child(3n + 3) {
				.latest_articles_slide_image::before {
					background-color: var(--green_color);
				}
			}
		}

		.is-destroy & {
			&:nth-child(3n + 1) {
				.latest_articles_slide_image::before {
					background-color: var(--orange_color);
				}
			}

			&:nth-child(3n + 2) {
				.latest_articles_slide_image::before {
					background-color: var(--green_color);
				}
			}

			&:nth-child(3n + 3) {
				.latest_articles_slide_image::before {
					background-color: var(--blue_color_3);
				}
			}
		}
	}

	@include bp($point_10 - 1) {
		&:nth-child(3n + 1) {
			.latest_articles_slide_image::before {
				background-color: var(--orange_color);
			}
		}

		&:nth-child(3n + 2) {
			.latest_articles_slide_image::before {
				background-color: var(--green_color);
			}
		}

		&:nth-child(3n + 3) {
			.latest_articles_slide_image::before {
				background-color: var(--blue_color_3);
			}
		}
	}

	@include bp($point_4, min) {
		padding: 0 2.6rem;
	}

	@include bp($point_4 - 1) {
		padding: 0 1rem;
	}
	.latest_articles__slider.is-destroy & {
		@include bp($point_2, min) {
			width: 25% !important;
		}
		@include bp($point_4 - 1) {
			width: 100% !important;
		}
	}
}

.latest_articles_slide_image {
	position: relative;

	display: block;
	width: 100%;
	padding-bottom: 100%;
	overflow: hidden;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		z-index: 2;

		width: 2.7rem;
		height: 2.7rem;

		transform: translate(100%, -100%);
		@extend %transition_transform;
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.9rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 3.3rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;

		@include bp($point_2, min) {
			&::before {
				transform: translate(0, 0);
			}
		}
	}
}

.latest_articles_slide_image__wrap {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;

	width: 100%;
	height: 100%;
}

.latest_articles_slide_image__in {
	@extend %cover_img;
}

.latest_articles_slide__info {
	@include flex_block(row, nowrap, flex-start, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}
}

.latest_articles_slide__item {
	@extend %slide_info_item;
}

.latest_articles_slide__count {
	&::before {
		content: ", ";
		display: inline-block;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.latest_articles_slide__item--link {
	@extend %slide_info_link;
}

.latest_articles_slide__link {
	display: block;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_2, min) {
		max-width: 27.6rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 29rem;
	}

	@include bp($point_4 - 1) {
		max-width: 24rem;
	}
}

.latest_articles_slide__title {
	display: block;

	color: var(--primary_color);
	font-family: $biotif_font;
	font-weight: 600;
	line-height: 1.3;
	letter-spacing: -0.03em;

	@extend %transition_color;

	@include bp($point_2, min) {
		font-size: 2.6rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 2.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}
}

.latest_articles_slide__title:hover,
.latest_articles_slide__title:focus {
	text-decoration: none;

	color: var(--primary_light_color);
}

// Progress bar
.latest_articles_bottom {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.latest_articles_bottom__value {
	font-family: $biotif_font;
	color: var(--blue_color);
	font-size: 1.3rem;
}

.latest_articles_bottom__progress {
	@extend %slider_nav_progress;
}

// arrows
.latest_articles__navigation {
	@extend %slider_nav;
}

.latest_articles__arrow {
	@extend %slider_nav_arrow;
}

.latest_articles__icon {
	@extend %slider_nav_icon;

	.latest_articles__arrow--prev & {
		transform: rotate(-180deg);
		margin-left: -0.3rem;
	}

	.latest_articles__arrow--next & {
		margin-right: -0.3rem;
	}
}

.latest_articles__navigation.hidden {
	display: none;
}
